import Note from './components/Note'
import { useEffect, useState } from 'react'
import { Button, Card, Table } from 'antd'

import { GET_ALL_NOTES, ADD_NOTE } from './services/notes'
import { GET_ALL_BLOGS, ADD_BLOG } from './services/blogs'

function randomString(e) {
  e = e || 32
  var t = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678',
    a = t.length,
    n = ''
  for (let i = 0; i < e; i++) n += t.charAt(Math.floor(Math.random() * a))
  return n
}
const { Meta } = Card
const App = () => {
  const [blogs, setBlogs] = useState([])
  useEffect(() => {
    getData()
  }, [])

  const addBlog = async (event) => {
    event.preventDefault()
    const noteObject = {
      title: randomString(10),
      author: randomString(6),
      url: randomString(32),
      likes: 0,
    }
    await ADD_BLOG(noteObject)

    // setBlogs(blogs.concat(newNote))
    getData()
  }

  const getData = async () => {
    const data = await GET_ALL_BLOGS()
    setBlogs(data)
  }
  console.log(blogs, 'blogs')

  return (
    <div>
      <h1>Blogs</h1>
      <Button onClick={addBlog}>add Blog</Button>
      <div style={{ display: 'flex', gap: 10 }}>
        {blogs.map((blog) => (
          <Card
            hoverable
            style={{ width: 240 }}
            key={blog.id}
            cover={
              <img
                alt="example"
                src="https://os.alipayobjects.com/rmsportal/QBnOOoLaAfKPirc.png"
              />
            }
          >
            <Meta title={blog.title} description={blog.url} />
          </Card>
        ))}
      </div>
    </div>
  )
}

export default App
